import { uniPopup } from '@dcloudio/uni-ui';
import step from '@/components/step.vue';
import requestw from '@/utils/requestw.js';
import allApiStr from '@/utils/allApiStr.js';

export default {
	data() {
		return {
			count: '',
			count_session_id: ''
		};
	},
	computed: {
		countList() {
			return this.count ? this.count.split('') : [];
		}
	},
	components: {
		uniPopup,
		step
	},
	/**
	 * 周期
	 */
	onLoad() {},
	onShow() {},
	onReady() {},
	onHide() {},
	onUnload() {},
	onPullDownRefresh() {},
	onReachBottom() {},
	onPageScroll() {},
	onBackPress() {},
	/**
	 * 方法
	 */
	methods: {
		async clickNext() {
			let res = await this.getVoiceCount();
			console.log(res);
			if (res.code !== 200) {
				uni.showToast({
					title: res.systemMessage,
					icon: 'none',
					mask: true
				});
				return;
			}

			this.count = res.data.code;
			this.count_session_id = res.data.session_id;
			this.$refs.countModal.open();
		},
		//获取语音码
		getVoiceCount() {
			return new Promise(async resolve => {
				this.count = '';
				this.count_session_id = '';
				uni.showLoading({
					title: '请稍候...',
					mask: true
				});
				let res = await requestw({
					url: allApiStr.getVoiceCountApi
				});
				uni.hideLoading();
				resolve(res.data);
			});
		},
		//录制视频
		luzhi() {
			const self = this;
			uni.chooseVideo({
				sourceType: ['camera'],
				compressed: true,
				maxDuration: 12, //最长12秒
				camera: 'front', //APP、微信小程序
				success: res => {
					console.log(res);
					if (res && res.errMsg.indexOf(':ok') > -1) {
						let tempFilePath = res.tempFilePath;
						self.uploadVideo(tempFilePath);
					}
				}
			});
		},
		//上传视频（百度H5活体校验）
		uploadVideo(tempFilePath) {
			const self = this;
			uni.showLoading({
				title: '请稍候...',
				mask: true
			});
			uni.uploadFile({
				url: allApiStr.checkVideoApi,
				filePath: tempFilePath,
				name: 'file',
				formData: { session_id: self.count_session_id },
				success: res => {
					uni.hideLoading();

					self.$refs.countModal.close();

					//待着结果json跳转
					uni.navigateTo({
						url: `/pages/individual/videoresult/videoresult?result=${res.data}`
					});
				}
			});
		}
	}
};
